.buttons {
    display: flex;
    flex-wrap: wrap;
}

.buttons__button {
    background: transparent;
    color: teal;
    border: none;
    margin: 0 0 0 20px;
    transition: color 0.4s;
}

@media(max-width:767px) {
    .buttons__button {
        flex-basis: 100%;
        margin: 10px;
    }
}

.buttons__button:disabled {
    color: #ccc;
}

.buttons__button:hover {
    color: rgb(2, 110, 110);
    cursor: pointer;
}