.tasks {
    padding: 10px;
    list-style: none;
    padding: 0px;
}

.tasks__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 10px;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid rgb(240, 238, 238);
}

.tasks__item--hidden {
    display: none;
}

.tasks__button {
    height: 30px;
    width: 30px;
    color: white;
    border: none;
    padding: 0;
    transition: background 0.3s;
}

.tasks__button--toggleDone {
    background: green;

}

.tasks__button--toggleDone:hover {
    background-color: rgb(5, 168, 5);

}

.tasks__button--toggleDone:active {
    background-color: rgb(7, 194, 7);

}

.tasks__content--done {
    text-decoration: line-through;
}

.tasks__button--remove {
    background-color: red;

}

.tasks__button--remove:hover {
    background-color: rgb(218, 82, 91);

}

.tasks__button--remove:active {
    background-color: rgb(100, 2, 2);

}